import VlIcon from '@govflanders/vl-ui-icon/src/vue';
import nlBE from './locale/nl-BE';

export default {
  i18n: {
    locale: 'nl-BE',
    messages: {
      'nl-BE': nlBE,
    },
  },
  components: {
    VlIcon,
  },
  props: {
    tagName: {
      type: String,
      default: 'span',
    },
    modSmall: Boolean,
    icon: String,
  },
  computed: {
    classes() {
      return [
        'vl-annotation',
        {
          'vl-annotation--small': this.modSmall,
        },
      ];
    },
  },
};
