import { VlIcon } from '@govflanders/vl-ui-icon/src/vue';

import nlBE from './locale/nl-BE';
import frBE from './locale/fr-BE';
import enGB from './locale/en-GB';

export default {
  i18n: {
    locale: 'nl-BE',
    messages: {
      'nl-BE': nlBE,
      'fr-BE': frBE,
      'en-GB': enGB,
    },
  },
  components: {
    VlIcon,
  },
  props: {
    tagName: {
      type: String,
      default: 'div',
    },
    icon: String,
    title: {
      type: String,
    },
    image: String,
    closeText: String,
    content: String,
    closable: Boolean,
    modSmall: Boolean,
    modSuccess: Boolean,
    modWarning: Boolean,
    modError: Boolean,
    modCta: Boolean,
    modLarge: Boolean,
    modNaked: Boolean,
  },
  data() {
    return {
      modFadeOut: false,
      visible: true,
    };
  },
  computed: {
    classes() {
      return [
        'vl-alert',
        {
          'vl-alert--small': this.modSmall,
          'vl-alert--success': this.modSuccess,
          'vl-alert--warning': this.modWarning,
          'vl-alert--error': this.modError,
          'vl-alert--cta': this.modCta,
          'vl-alert--fade-out': this.modFadeOut,
          'vl-alert--large': this.modLarge,
          'vl-alert--naked': this.modNaked,
        },
      ];
    },
    titleContentClasses() {
      return [
        'vl-alert__title',
        {
          'vl-u-mark--success': this.modSuccess && this.modNaked,
          'vl-u-mark--warning': this.modWarning && this.modNaked,
          'vl-u-mark--error': this.modError && this.modNaked,
        },
      ];
    },
    messageClasses() {
      return [
        'vl-alert__message',
        'vl-typography',
      ];
    },
    nakedClasses() {
      return {
        'vl-u-mark--success': this.modSuccess && this.modNaked,
        'vl-u-mark--warning': this.modWarning && this.modNaked,
        'vl-u-mark--error': this.modError && this.modNaked,
      };
    },
    slotAction() {
      return this.$slots.actions && this.$slots.actions.length;
    },
  },
  methods: {
    close() {
      this.$emit('close');
    },
  },
};
