/* eslint no-underscore-dangle: [2, { 'allow': ['_componentTag',  '_isVue'] }] */
import VlIcon from '@govflanders/vl-ui-icon/src/vue';
import nlBE from './locale/nl-BE';

const defaultTagName = 'div';

export default {
  props: {
    i18n: {
      locale: 'nl-BE',
      messages: {
        'nl-BE': nlBE,
      },
    },
    tagName: {
      type: String,
      default: defaultTagName,
    },
    href: String,
    to: null,
    title: {
      type: String,
    },
    subtitle: String,
    text: String,
    image: {
      src: String,
      srcset: String,
      alt: String,
      position: {
        type: String,
        default: 'center-center',
      },
    },
    data: {
      value: String,
      subvalues: Array,
    },
    modSmall: Boolean,
    modLarge: Boolean,
    modDocument: Boolean,
    modNoBorder: Boolean,
    modAlt: Boolean,
    modColorBlockAlt: Boolean,
    modColorBlockDark: Boolean,
    colorBlock: Boolean,
    transparentBlock: Boolean,
    icon: String,
  },
  components: {
    VlIcon,
  },
  computed: {
    tag() {
      let tag = this.tagName;
      if (this.to && this.tagName === defaultTagName) {
        tag = 'nuxt-link';
      } else if (this.href) {
        tag = 'a';
      }
      return tag;
    },
    classes() {
      return [
        'vl-spotlight', {
          'vl-spotlight--s': this.modSmall,
          'vl-spotlight--l': this.modLarge,
          'vl-spotlight--alt': this.modAlt,
          'vl-spotlight--no-border': this.modNoBorder,
          'vl-spotlight--image': !!this.image || !!this.transparentBlock || !!this.colorBlock,
        },
      ];
    },
    imageClasses() {
      const imgClasses = ['vl-spotlight__image'];

      if (this.image) {
        if ('position' in this.image) {
          imgClasses.push(`vl-spotlight__image--focus-${this.image.position}`);
        }
      }
      return imgClasses;
    },
    transparentBlockClasses() {
      const classes = ['vl-spotlight__transparent-block'];

      if (this.image) {
        classes.push('vl-spotlight__transparent-block--visual');
      }

      return classes;
    },
    colorBlockClasses() {
      const classes = ['vl-spotlight__color-block'];

      if (this.modColorBlockAlt) {
        classes.push('vl-spotlight__color-block--alt');
      }

      if (this.modColorBlockDark) {
        classes.push('vl-spotlight__color-block--dark');
      }

      return classes;
    },
    showDefaultSlot() {
      return !this.text && !this.modDocument;
    },
  },
};
