import nlBE from './locale/nl-BE';

export default {
  i18n: {
    locale: 'nl-BE',
    messages: {
      'nl-BE': nlBE,
    },
  },
  props: {
    tagName: {
      type: String,
      default: 'table',
    },
    modMatrix: Boolean,
    modLine: Boolean,
    modZebra: Boolean,
    modHover: Boolean,
    modCollapsedXs: Boolean,
    modCollapsedS: Boolean,
    modCollapsedM: Boolean,
  },
  // Computed properties
  computed: {
    classes() {
      return [
        'vl-data-table', {
          'vl-data-table--matrix': this.modMatrix,
          'vl-data-table--lined': this.modLine,
          'vl-data-table--zebra': this.modZebra,
          'vl-data-table--hover': this.modHover,
          'vl-data-table--collapsed-xs': this.modCollapsedXs,
          'vl-data-table--collapsed-s': this.modCollapsedS,
          'vl-data-table--collapsed-m': this.modCollapsedM,
        },
      ];
    },
  },
};
