import VlIcon from '@govflanders/vl-ui-icon/src/vue/';
import nlBE from './locale/nl-BE';

const defaultTagName = 'router-link';

export default {
  i18n: {
    locale: 'nl-BE',
    messages: {
      'nl-BE': nlBE,
    },
  },
  components: {
    VlIcon,
  },
  props: {
    tagName: {
      type: String,
      default: defaultTagName,
    },
    to: null,
    href: null,
    exact: Boolean,
    icon: String,
    hiddenText: String,
    modIconBefore: Boolean,
    modIconAfter: Boolean,
    modIconOnly: Boolean,
    modIconLight: Boolean,
    modIconRotatedHalf: Boolean,
    modIconRotatedFull: Boolean,
    modBlock: Boolean,
    modBold: Boolean,
    modNoClass: Boolean,
    modSmall: Boolean,
    modLarge: Boolean,
  },
  data() {
    return {
      url_href: this.href,
      url_to: this.to,
    };
  },
  computed: {
    classes() {
      return {
        'vl-link': !this.modNoClass,
        'vl-link--block': this.modBlock,
        'vl-link--bold': this.modBold,
        'vl-link--small': this.modSmall,
        'vl-link--large': this.modLarge,
      };
    },
    iconClasses() {
      return [
        'vl-link__icon', {
          'vl-link__icon--before': this.modIconBefore,
          'vl-link__icon--after': this.modIconAfter,
          'vl-link__icon--light': this.modIconLight,
        },
      ];
    },
    inputListeners() {
      return Object.assign(
        {},
        this.$listeners,
      );
    },
    tag() {
      if (this.tagName !== defaultTagName) {
        return this.tagName;
      }
      if ((this.to && this.to.match(/^(http(s)?):\/\/|ftp:|mailto:|tel:/)) || this.href || (!this.to && !this.href)) {
        this.url_href = this.href || this.to;
        this.url_to = null;
        return 'a';
      }
      return defaultTagName;
    },
  },
};
