import VlIcon from '@govflanders/vl-ui-icon/src/vue';
import nlBE from './locale/nl-BE';

const defaultTagName = 'button';

export default {
  i18n: {
    locale: 'nl-BE',
    messages: {
      'nl-BE': nlBE,
    },
  },
  components: {
    VlIcon,
  },
  props: {
    tagName: {
      type: String,
      default: defaultTagName,
    },
    icon: {
      type: String,
    },
    to: null,
    href: null,
    modDisabled: Boolean,
    modError: Boolean,
    modIcon: Boolean,
    modIconAfter: Boolean,
    modIconBefore: Boolean,
    modNarrow: Boolean,
    modBlock: Boolean,
    modLarge: Boolean,
    modLoading: Boolean,
    modWide: Boolean,
    modSecondary: Boolean,
    modTertiary: Boolean,
    modNaked: Boolean,
  },
  data() {
    return {
      urlHref: this.href,
      urlTo: this.to,
    };
  },
  computed: {
    tag() {
      if (this.tagName !== defaultTagName) {
        return this.tagName;
      }
      if ((this.to && this.to.match(/^(http(s)?):\/\/|ftp:|mailto:|tel:/))) {
        this.urlHref = this.href || this.to;
        this.urlTo = null;
        return 'a';
      }
      if (this.to) {
        return 'router-link';
      }
      return defaultTagName;
    },
    classes() {
      return [
        'vl-button',
        {
          'vl-button--disabled': this.modDisabled,
          'vl-button--error': this.modError,
          'vl-button--icon': this.modIcon,
          'vl-button--icon-after': this.modIconAfter,
          'vl-button--icon-before': this.modIconBefore,
          'vl-button--narrow': this.modNarrow,
          'vl-button--block': this.modBlock,
          'vl-button--large': this.modLarge,
          'vl-button--loading': this.modLoading,
          'vl-button--wide': this.modWide,
          'vl-button--secondary': this.modSecondary,
          'vl-button--tertiary': this.modTertiary,
          'vl-button--naked': this.modNaked,
        },
      ];
    },
    inputListeners() {
      return Object.assign(
        {},
        this.$listeners,
      );
    },
  },
};
