export default {
  props: {
    tagName: {
      type: String,
      default: 'div',
    },
    modStacked: Boolean,
    modStart: Boolean,
    modCenter: Boolean,
    modEnd: Boolean,
    modSpaceBetween: Boolean,
    modSpaceAround: Boolean,
    modNoGutter: Boolean,
    modVStretched: Boolean,
    modVTop: Boolean,
    modVCenter: Boolean,
    modVBottom: Boolean,
    modWide: Boolean,
  },
  computed: {
    classes() {
      return [
        'vl-grid',
        {
          'vl-grid--is-stacked': this.modStacked,
          'vl-grid--no-gutter': this.modNoGutter,
          'vl-grid--align-start': this.modStart,
          'vl-grid--align-center': this.modCenter,
          'vl-grid--align-end': this.modEnd,
          'vl-grid--align-space-between': this.modSpaceBetween,
          'vl-grid--align-space-around': this.modSpaceAround,
          'vl-grid--v-stretched': this.modVStretched,
          'vl-grid--v-center': this.modVCenter,
          'vl-grid--v-top': this.modVTop,
          'vl-grid--v-bottom': this.modVBottom,
          'vl-grid--wide': this.modWide,
        },
      ];
    },
  },
};
