import VlGrid from '../vl-grid/vl-grid.vue';

export default {
  extends: VlGrid,
  computed: {
    classes() {
      return [
        'vl-form-grid',
        {
          'vl-form-grid--is-stacked': this.modStacked,
          'vl-form-grid--no-gutter': this.modNoGutter,
          'vl-form-grid--align-start': this.modStart,
          'vl-form-grid--align-center': this.modCenter,
          'vl-form-grid--align-end': this.modEnd,
          'vl-form-grid--align-space-between': this.modSpaceBetween,
          'vl-form-grid--align-space-around': this.modSpaceAround,
          'vl-form-grid--v-stretched': this.modVStretched,
          'vl-form-grid--v-center': this.modVCenter,
          'vl-form-grid--v-top': this.modVTop,
          'vl-form-grid--v-bottom': this.modVBottom,
        },
      ];
    },
  },
};
