import nlBE from './locale/nl-BE';

export default {
  i18n: {
    locale: 'nl-BE',
    messages: {
      'nl-BE': nlBE,
    },
  },
  props: {
    tagName: {
      type: String,
      default: 'div',
    },
    message: String,
    modDark: Boolean,
    modMessageHidden: {
      type: Boolean,
      default: false,
    },
  },
  // Computed properties
  computed: {
    classes() {
      return [
        'vl-loader', {
          'vl-loader--light': this.modDark,
        },
      ];
    },
  },
};
