import VlIcon from '@govflanders/vl-ui-icon/src/vue';
import nlBE from './locale/nl-BE';

export default {
  i18n: {
    locale: 'nl-BE',
    messages: {
      'nl-BE': nlBE,
    },
  },
  components: {
    VlIcon,
  },
  props: {
    id: String,
    tagName: {
      type: String,
      default: 'details',
    },
    title: String,
    titleOpen: String,
    open: Boolean,
    icon: {
      type: String,
      default: 'arrow-right-fat',
    },
    iconOpen: String,
    modMore: Boolean,
    modAltBg: Boolean,
  },
  data() {
    return {
      opened: this.open,
      hasLink: true,
    };
  },
  // Computed properties
  computed: {
    classes() {
      return [
        'vl-toggle', {
          'vl-link': this.hasLink && !this.modMore,
          'vl-link--bold': this.hasLink && !this.modMore,
          'vl-toggle--more-button': this.modMore,
          'vl-button': this.modMore,
          'vl-button--secondary': this.modMore,
        },
      ];
    },
    summaryClasses() {
      return [
        {
          'vl-toggle-wrapper--more-button': this.modMore,
        },
      ];
    },
    wrapperClasses() {
      return [
        'js-vl-accordion', {
          'js-vl-accordion--open': this.opened,
          'js-vl-accordion--flex-reverse': this.modMore,
        },
      ];
    },
    pannelClasses() {
      return [
        'vl-accordion__panel',
        {
          'vl-accordion__panel--alt': this.modAltBg,
        },
      ];
    },
    hasAdvancedSummary() {
      return !!this.$slots.title;
    },
    openedString() {
      return this.opened ? 'true' : 'false';
    },
    closedString() {
      return !this.opened ? 'true' : 'false';
    },
    calculatedIcon() {
      // eslint-disable-next-line
      let icon = this.icon; // don't use object destructuring because that won't work here

      if (this.opened) {
        icon = this.iconOpen ? this.iconOpen : this.icon;
      }
      return icon;
    },
    detailsTagName() {
      return this.modMore ? 'div' : this.tagName;
    },
    accordionTagName() {
      // Because summary doesn't allow flex-direction: column-reverse
      return this.modMore ? 'div' : 'summary';
    },
    accordionTitle() {
      // eslint-disable-next-line
      let title = this.title; // don't use object destructuring because that won't work here

      if (this.opened) {
        title = this.titleOpen ? this.titleOpen : this.title;
      }

      return title;
    },
  },
  methods: {
    accordionToggle() {
      this.opened = !this.opened;

      if (this.opened) {
        this.accordionOpen();
      } else {
        this.accordionClose();
      }
    },
    accordionOpen() {
      this.opened = true;

      this.$emit('accordion.opened', this.$el);
    },
    accordionClose() {
      this.opened = false;

      this.$emit('accordion.closed', this.$el);
    },
    hashChange() {
      if (window.location.hash) {
        if (`#${this.id}` === window.location.hash) {
          this.accordionOpen();
        }
      }
    },
  },
  mounted() {
    if (typeof window !== 'undefined') {
      this.hashChange();

      window.addEventListener('hashchange', () => {
        this.hashChange();
      });
    }
  },
};
